#root, html, body {
    margin: 0;
    padding: 0;
    height: 100vh;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
  }
  
  .app_advise {
    height: 100%;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .card_advise {
    background: #F06779;
    width: 50%;
    height: 25%;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 25px;
    padding: 20px 40px;
    color: white;
    font-weight: 600;
    font-size: 18px;

  }
  
  .heading_advise {
    display: flex;
    align-items: center;
    height: 582px;
  }
  
  .buttonAdvice {
    outline: none;
    text-decoration: none;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-transform: uppercase;
    height: 200px;
    width: 210px;
    opacity: 1;
    background-color: #ffffff;
    border: 1px solid rgba(22, 76, 167, 0.6);
  }
    
  .span_advise {
    color: #164ca7;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.7px;
  }
    
  

  