.NavbarMain{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
}
.NavbarLogo{
    width: 150px;
}
.NavbarLogo > img{
    width: 100%;
    object-fit: cover;
}
.Pages{
    display: flex;
}
.link{
    padding: 8px 16px;
    margin-right: 15px;
    cursor: pointer;
    list-style: none;
    font-size: 16px;
    color: black;
    font-weight: 600;
    text-decoration: none;
}
.link:hover{
    color: #F06779;
    transition: ease-in-out 0.2s;
}